import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { HowToPlayPage } from '../pages/how-to-play/how-to-play';
import { NextGamePage } from '../pages/next-game/next-game';
import { HowToPlayPageModule } from '../pages/how-to-play/how-to-play.module';
import { NextGamePageModule } from '../pages/next-game/next-game.module';


@NgModule({
  declarations: [
    MyApp,
    HomePage,
   // HowToPlayPage,
    //NextGamePage
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    IonicModule.forRoot(MyApp),
    HowToPlayPageModule,
    NextGamePageModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    NextGamePage,
    HowToPlayPage

  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {}



